<template>
    <div class="container">
        <div class="main__users" v-if="posts">
            <div class="main__users-item" v-for="post in posts" :key="post.id">
                <p class="first__text">{{ post.title }}</p>
                <p>{{ post.body }}</p>
            </div>
        </div>
        <div v-else>
            Идет загрузка...
        </div>
    </div>
</template>

<script>
    export default {
       data() {
            return {
               
            }
       },
       created() {
        this.getPosts()
       },
       computed:{
        posts(){
            return this.$store.state.posts
        }
       },
       methods: {
        getPosts(){
            this.$store.dispatch('getPosts')
        } 
       },
    }
</script>